@import './variables.scss';

html {}

body {
  margin: 0;
  height: 100%;
  max-width: 100vw;
  font-family: 'font-bold';
}

a {
  color: #FFF;

  &:focus, &:hover, &:visited, &:link, &:active {
    text-decoration: none;
    outline: 0;
    border: none;
    -moz-outline-style: none;
    -webkit-tap-highlight-color: transparent;
  }
}

span {
  color: #8c8c91;
  font-weight: bold;
  display: block;
  font-size: calc(12px + 0.05vw);
}

p {
  margin: 0;
  font-size: 26px;
  line-height: 34px;

  @media #{$small} {
    font-size: 20px;
    line-height: 28px;
  }

  @media #{$ipad-pro-portrait} {
    font-size: 38px;
    line-height: 50px;
  }

  @media #{$ipad-landscape} {
    font-size: 22px;
    line-height: 28px;
  }
}

h1 {
  font-size: 62px;
  margin: 0;

  @media #{$small} {
    font-size: 28px;
  }

  @media #{$ipad-pro-portrait} {
    font-size: 72px;
    line-height: 76px;
  }

  @media #{$ipad-landscape} {
    font-size: 42px;
    line-height: 48px;
  }
}

h2 {
  font-size: 52px;
  line-height: 50px;

  @media #{$small} {
    font-size: 34px;
    line-height: 38px;
  }

  @media #{$ipad-landscape} {
    font-size: 42px;
    line-height: 48px;
  }

  @media #{$ipad-pro-portrait} {
    font-size: 72px;
    line-height: 76px;
  }
}

h3 {
  font-size: 32px;

  @media #{$small} {
    font-size: 24px;
  }

  @media #{$ipad-pro-portrait} {
    font-size: 38px;
    line-height: 50px;
    margin-bottom: 0;
  }
}

h6 {
  @media #{$small} {
    font-size: 16px;
  }
}

.App {
  height: 100%;
  width: 100%;
}

